#layer {
  position: absolute;
  top: -2vh;
  width: 35vw;
  height: 100vh;
  background: #0000;
  transition: 0.8s;
  transition-delay: 0.2s;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &.signin {
    left: 63vw;
    
    .background {
      left: -63vw;
    }
  }
  
  &.layer-signin-anim {
    animation: layerAnim 1s ease-in-out;
  }
  
  &.signup {
    left: -2vw;
    
    .background {
      left: -2vw;
    }
  }
  
  &.layer-signup-anim {
    animation: layerAnim 1s ease-in-out reverse;
  }
  
  .background {
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    transition: 0.8s;
    transition-timing-function: ease-in-out;
    transition-delay: 0.1s;
    background-image: linear-gradient(to top right, #44c298, #40abb4);

    h1, .text {
      color: #e9e8e8;
    }
    
    .content {
      // border: 1px solid orange;
      position: relative;
      margin-top: 34vh;
      
      .signin-area {
        width: 35%;
        text-align: center;
        margin-bottom: 2vh;
        position: absolute;
        right: 0;
        transition: 0.5s;
        
        &.active {
          right: 2vw;
        }
        
        h1 {
          font-size: 270%;
          font-weight: 400;
          margin-bottom: 2vh;
        }
        
        .text {
          width: 210px;
          margin: 0 auto;
          line-height: 3.5vh;
        }
      }
      
      .signup-area {
        width: 35%;
        text-align: center;
        margin-bottom: 2vh;
        position: absolute;
        left: 0;
        transition: 0.5s;
        
        &.active {
          left: 2vw;
        }
        
        h1 {
          font-size: 270%;
          font-weight: 400;
          margin-bottom: 2vh;
        }
        
        .text {
          width: 250px;
          margin: 0 auto;
          line-height: 3.5vh;
        }
      }
    }
  }

  button {
    display: block;
    width: 260px;
    height: 60px;
    font-size: 115%;
    background: transparent;
    border: 2px solid #f4f4f4;
    // padding: 18px;
    text-align: center;
    letter-spacing: 2px;
    font-weight: 100;
    border-radius: 999px;
    color: #f4f4f4;
    cursor: pointer;
    margin-top: 22vh;
    position: relative;
    overflow: hidden;

    &.button-signin-anim {
      animation: jhfskjvkshfv 1s ease-in-out;
    }
    
    &.button-signup-anim {
      animation: jhfskjvkshfv 1s ease-in-out;
    }

    .text {
      // border: 1px solid black;
      width: 260px;
      height: 26px;
      position: absolute;
      top: 17px;
      transition: 1s;
      transition-timing-function: ease-in-out;
      
      &.signin {
        left: -90px;

        .signin-text {
          opacity: 0;
        }
      }
      
      &.signup {
        left: 90px;
        
        .signup-text {
          opacity: 0;
        }
      }

      .signin-text {
        float: left;
        width: 80px;
        // border: 1px solid red;
      }
      
      .signup-text {
        float: right;
        width: 80px;
        // border: 1px solid red;
      }
    }
  }
}

@keyframes jhfskjvkshfv {
  0% {
    width: 260px;
  }
  10% {
    width: 260px;
  }
  50% {
    width: 450px;    
  }
  90% {
    width: 260px;
  }
  100% {
    width: 260px;
  }
}

@keyframes layerAnim {
  0% {
    width: 35vw;
    left: -2vw;
  }
  10% {
    left: -2vw;
  }
  50% {
    width: 60vw;
  }
  90% {
    left: 63vw;
  }
  100% {
    width: 35vw;
    left: 63vw;
  }
}


