#social-icons {
  display: flex;
  justify-content: center;
  margin: {
    bottom: 10px;
  }

  .icon {
    width: 50px;
    height: 50px;
    margin: 18px;
    border-radius: 9999px;
    background: transparent;
    border: 1px solid #3337;
    display: inline-block;
    text-align: center;
    font-size: 20px;
    padding-top: 9px;
    cursor: pointer;
    
    &:first-child {
      margin-left: 0;
    }
    
    &:last-child {
      margin-right: 0;
    }
  }
}